import React, { useEffect, useState } from "react";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';

import "./footer.css";
function FAQS() {
  const calculateTimeLeft = () => {
    let year = new Date().getFullYear();
    const difference = +new Date(`${year}-7-4`) - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
  const [year] = useState(new Date().getFullYear());
  var firstDigit = String(timeLeft.days).charAt(0);
  var secondDigit = String(timeLeft.days).charAt(1);

  var FIRST = Number(firstDigit);

  var firstDigitx = "images/numbers/" + firstDigit + ".jpg";
  var secondDigitx = "images/numbers/" + secondDigit + ".jpg";

  useEffect(() => {
    setTimeout(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);
  });

  const timerComponents = [];

  Object.keys(timeLeft).forEach((interval) => {
    if (!timeLeft[interval]) {
      return;
    }

    timerComponents.push(
      <span>
        {timeLeft[interval]} {interval}{" "}
      </span>
    );
  });

  return (
    <>
      {/*  This site was created by BelleIT.net  */}
      {/*  WordPress was greatly considered in the creation of this site  */}
      {/*  Last Published: Thu Oct 22 2020 19:29:17 GMT+0000 (Coordinated Universal Time)  */}
      <meta charSet="utf-8" />
      <title>Real Home</title>
      <meta content="Real Home" property="og:title" />
      <meta content="Real Home" property="twitter:title" />
      <meta content="width=device-width, initial-scale=1" name="viewport" />
      <meta content="Webflow" name="generator" />
      <link href="css/normalize.css" rel="stylesheet" type="text/css" />
      <link href="css/webflow.css" rel="stylesheet" type="text/css" />
      <link href="https://fonts.googleapis.com/icon?family=Material+Icons"
      rel="stylesheet">
    </link>
      <link
        href="css/perspectivebfa.webflow.css"
        rel="stylesheet"
        type="text/css"
      />
      {/* [if lt IE 9]><![endif] */}
      <link href="images/favicon.ico" rel="shortcut icon" type="image/x-icon" />
      <link href="images/webclip.png" rel="apple-touch-icon" />
      
      <div className="section center">
        <div className="w-container">
          <section id="contact" className="home-section bg-gray paddingbot-90">
            <div className="text-white"><strong><p>Countdown until launch:</p></strong></div>
            <p className="label text-white" style={{opacity:'33%'}}>
            {timerComponents.length ? timerComponents : <p>Live!</p>}
          </p>
          <marquee className= 'banner ' direction='right'  style= {{position:'inline-block'}} >
          
          <span class="material-icons">
          build
</span>
</marquee>
          
        </section>
      </div>
    </div>
    </>
  );
}

export default FAQS;
