import React, { Component, useRef, useState, useEffect } from "react";


import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import About from "./components/about";
import P from "./components/P";
import Slider from "./components/Slider";
import Images from "./images";

import Cover from "./components/Cover";

import Contact from './components/contact';
import FAQS from './components/Faqs';

import './index.css';


import ScrollToTop2 from "./components/ScrollToTop2";
import Clima from "./components/Clima";



class App extends Component {
  render() {
  return (
    <>
     
  <BrowserRouter >


        <ScrollToTop2>
        
          <Switch>
            <Route
              exact
              path="/"
              render={(props) => <Cover/>}/>
            <Route
              exact
              path={process.env.PUBLIC_URL + "/about"}
              render={(props) => <About/>}
            ></Route>
            <Route
              exact
              path={process.env.PUBLIC_URL + "/portfolio"}
              render={(props) => <Slider images={Images} />}
            ></Route>
            <Route exact path="/P:num" render={(props) => <P/>}></Route>
            <Route  exact path="/clima" render={(props) => <Clima/>}></Route>

            <Route exact path="/contact" render={(props) => <Contact/>}></Route>
            <Route exact path="/test" render={(props) => <FAQS/>}></Route>


          </Switch>
          
        </ScrollToTop2>
        </BrowserRouter>
        
    </>
   )
  }
}

export default App;